  border: 1px solid #ffffff;
}
.feedback__select-wrapper:hover button svg {
  stroke: #ffffff;
}
.feedback__select-wrapper:focus-within .feedback__select-label {
  color: #f0efe9;
}
.feedback__select-wrapper:focus-within .custom-select__button {
  border-color: white;
}
.feedback__select-wrapper:focus-within .custom-select__icon svg {
  stroke: white;
}
.feedback__select-wrapper:disabled input,
.feedback__select-wrapper:disabled textarea {
  pointer-events: none;
  opacity: 0.3;
}
.feedback__submit-button {
  margin: 22px 0 0;
  width: 100%;
  gap: 28px;
}
@media (max-width: 767px) {
  .feedback__submit-button {
    margin: 14px 0 0;
    gap: 22px;
  }
}
.feedback__submit-button .button__text {
  flex-grow: 1;
}
.feedback__submit-button .button__icon-wrapper {
  flex-shrink: 0;
}
.feedback__select-label {
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  color: rgba(240, 239, 233, 0.8);
}
@media (max-width: 767px) {
  .feedback__select-label {
    font-size: 12px;
  }
}
.feedback__custom-input label {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.feedback__custom-input span {
  line-height: 14px;
}
.feedback__custom-toggle {
  margin: 4px 0 0;
}

